import { ActionType } from "../../models/models";
import { SET_DESKTOP_MODE, SET_MOBILE_MODE } from "../actionType";

const initialState = {
  isDesktop: true, // По умолчанию считается, что приложение запущено на десктопе
};

export const adaptationReducer = (state = initialState, action: ActionType) => {
  switch (action.type) {
    case SET_DESKTOP_MODE:
      return {
        ...state,
        isDesktop: true,
      };
    case SET_MOBILE_MODE:
      return {
        ...state,
        isDesktop: false,
      };
    default:
      return state;
  }
};
