import {createAppAsyncThunk} from "../../utils/create-app-async-thunk";
import {UserParamsType, VerificationCodeType} from "../types";
import axios from "axios";
import {createSlice, PayloadAction} from "@reduxjs/toolkit";

interface RegistrationState {
    user: UserParamsType | null
    isRegistered: boolean;
    isRegisteredCode: boolean;
    isLoading: boolean;
    error: string | null;
}

const initialState: RegistrationState = {
    user: null,
    isRegistered: false,
    isRegisteredCode: false,
    isLoading: false,
    error: null,
};

export const registerUserAsync = createAppAsyncThunk(
    'registration/registerUser',
    async (userParams: UserParamsType) => {
        //временная логика
        const response = await axios.get('https://jsonplaceholder.typicode.com/posts/2');
        return userParams;
    }
);

export const registerCodeAsync = createAppAsyncThunk(
    'registration/registerCode',
    async (code: string) => {
        //временная логика
        const response = await axios.get('https://jsonplaceholder.typicode.com/posts/2');
        return code;
    }
);

const registrationSlice = createSlice({
    name: 'registration',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(registerUserAsync.pending, (state) => {
                state.isLoading = true;
                state.error = null;
            })
            .addCase(registerUserAsync.fulfilled, (state, action: PayloadAction<UserParamsType>) => {
                console.log('Зарегистрированный юзер в редаксе:', action.payload.name);
                state.user = action.payload;
                state.isRegistered = true;
                state.isLoading = false;
            })
            .addCase(registerUserAsync.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.error.message || "Failed to register";
            })
            .addCase(registerCodeAsync.pending, (state) => {
                state.isLoading = true;
                state.error = null;
            })
            .addCase(registerCodeAsync.fulfilled, (state, action: PayloadAction<string>) => {
                console.log('Зарегистрированный юзер в редаксе:', action.payload);
                state.isRegisteredCode = true;
                state.isLoading = false;
            })
            .addCase(registerCodeAsync.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.error.message || "Failed to register";
            });
    },
});

export const {} = registrationSlice.actions;
export default registrationSlice.reducer;

