import React from "react";
import Modal from "../Modal/Modal";
import {theme} from "../../styles/theme";
import {
    Body1_Regular,
    Body2_Medium, Body3_Bold, Body6_Regular,
    H2_Bold,
} from "../../components/Typography/Typography";
import styled from "styled-components";
import {Button} from "../../components/button/button";
import {Input} from "../../components/input/input";
import {Controller, useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {validationSchema} from "./validationSchema";
import {useDispatch, useSelector} from "react-redux";
import {toggleModal} from "../../store/modal/modalSlice";
import {RootState} from "../../store/store";
import {selectorAdaptation} from "../../store/selectors/selectorAdaptation";

export default function RegistrationPassword() {
    const dispatch = useDispatch();
    const isDesktopStore = useSelector(selectorAdaptation);
    /**состояние экрана мобилки*/
    const {user} = useSelector((state: RootState) => state.registration);
    const modalId = 6;
    const email = user?.email;

    const {
        control,
        reset,
        handleSubmit,
        formState: {errors},
    } = useForm({
        resolver: yupResolver(validationSchema),
    });

    const onSubmit = (data: any) => {
        const {password} = data;
        console.log(password, email);
        //dispatch(registerPasswordAsync(password, email));
        dispatch(toggleModal(modalId));
        reset();
    };

    return (
        <Modal id={modalId} showCloseButton={true}>
            {isDesktopStore ? (<ModalContent>
                    <Title>
                        <H2_Bold>
                            Создание пароля
                        </H2_Bold>
                    </Title>
                    <Body1_Regular>Пароль должен содержать не менее 10 символов</Body1_Regular>
                    <Form onSubmit={handleSubmit(onSubmit)}>
                        <Controller
                            name="password"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    type={"password"}
                                    {...field}
                                    width={"452px"}
                                    placeholder={"Введите пароль"}
                                    error={errors.password?.message}
                                />
                            )}
                        />
                        <Controller
                            name="passwordRepeat"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    type={"password"}
                                    {...field}
                                    width={"452px"}
                                    placeholder={"Повторите пароль"}
                                    error={errors.passwordRepeat?.message}
                                />
                            )}
                        />
                        <Body2_Medium>
                            <ButtonModal
                                width={"392px"}
                                color={theme.colors.secondary100}
                                border={false}
                                type="submit"
                            >
                                Продолжить
                            </ButtonModal>
                        </Body2_Medium>
                    </Form>
                </ModalContent>) :
                (<ModalContent>
                    <Title>
                        <Body3_Bold>
                            Создание пароля
                        </Body3_Bold>
                    </Title>
                    <Body6_Regular>Пароль должен содержать не менее 10 символов</Body6_Regular>
                    <Form onSubmit={handleSubmit(onSubmit)}>
                        <Controller
                            name="password"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    type={"password"}
                                    {...field}
                                    width={"312px"}
                                    placeholder={"Введите пароль"}
                                    error={errors.password?.message}
                                />
                            )}
                        />
                        <Controller
                            name="passwordRepeat"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    type={"password"}
                                    {...field}
                                    width={"312px"}
                                    placeholder={"Повторите пароль"}
                                    error={errors.passwordRepeat?.message}
                                />
                            )}
                        />
                        <Body2_Medium>
                            <ButtonModal
                                width={"312px"}
                                color={theme.colors.secondary100}
                                border={false}
                                type="submit"
                            >
                                Продолжить
                            </ButtonModal>
                        </Body2_Medium>
                    </Form>
                </ModalContent>)
            }
        </Modal>
    );
}

const ModalContent = styled.div`
    width: 772px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: white;
    @media ${theme.media.tablet} {
        /**адаптивная верстка 'screen and (max-width: 992px)',*/
        width: 100%;
        max-width: 576px;
    }
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        width: 100%;
        max-width: 360px;
    }
`;
const Title = styled.div`
    text-align: center;
    padding-top: 40px;
    padding-bottom: 40px;
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        max-width: 176px;
        padding-top: 0;
        padding-bottom: 20px;
    }
`;
const Form = styled.form`
    margin-top: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
`;
const ButtonModal = styled(Button)`
    margin-top: 8px;
`;
