import { styled } from 'styled-components';
import { theme } from '../../styles/theme';
import React from 'react';
import { Body2_Medium } from '../Typography/Typography';

type ButtonType = 'button' | 'submit' | 'reset'; //допустимые типы кнопки

type Props = {
  type?: ButtonType;
  className?: string;
  color: string;
  border: boolean;
  children: React.ReactNode;
  disabled?: boolean;
  fontSize?: string;
  width?: string;
  height?: string;
  onClick?: React.MouseEventHandler<HTMLButtonElement>;
};

const ButtonContainer = ({
  type,
  className,
  children,
  disabled,
  onClick,
}: Props) => {
  return (
    <button
      type={type}
      disabled={disabled}
      className={className}
      onClick={onClick}
    >
      {children}
    </button>
  );
};

export const Button = styled(ButtonContainer)`
  display: flex;
  justify-content: center;
  align-items: center;
  width: ${({ width = '329px' }) => width};
  height: ${({ height = '48px' }) => height};
  border: ${({ border }) => (border ? '1px solid #000' : 'none')};
  border-radius: 12px;
  font: ${theme.fonts.body2medium20};
  background-color: ${({ color }) => color};
  &:hover {
    cursor: ${({ disabled }) => (disabled ? 'default' : 'pointer')};
    background-color: rgba(${({ color }) => {
      if (color.slice(-2, -1) === '1') {
        return color.slice(5, -2) + '0.8';
      } else {
        return color.slice(5, -4) + (color.slice(-4, -1) - 0.2).toFixed(1);
      }
    }});
  &:disabled {
    opacity: 0.3;
  }
  &:active {
    background-color: rgba(${({ color }) => color.slice(5, -2)} 0.3);
  }
`;
