import {createSlice, PayloadAction} from '@reduxjs/toolkit';

export interface ModalState {
    selectModal: number[];
}

const initialState: ModalState = {
    selectModal: [],//храним здесь id открытых модальных окон
};

const modalSlice = createSlice({
    name: 'modal',
    initialState,
    reducers: {
        toggleModal: (state, action: PayloadAction<number>) => {
            const  modalId  = action.payload;//берем пришедший id
            //проверяем есть ли это id в состоянии
            const isOpen = state.selectModal.includes(modalId);
            //если есть удаляем
            if (isOpen) {
                state.selectModal = state.selectModal.filter(id => id !== modalId);
                //если нет добавляем
            } else {
                state.selectModal.push(modalId);
            }
        },
    },
});

export const { toggleModal } = modalSlice.actions;
export default modalSlice.reducer;
