import styled from "styled-components";

// Desktop Footer Styles
export const FooterContainer = styled.footer`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 100px;
  background-color: ${({ theme }) => theme.colors.primary100 || "#002e7a"};
  color: ${({ theme }) => theme.colors.backgroundWhite100 || "#fff"};
  font-size: 16px;
  height: 240px;
  min-height: 240px;
  box-sizing: border-box;

  @media ${({ theme }) => theme.media.mobile} {
    flex-direction: column;
    align-items: center;
    padding: 20px;
    font-size: 14px;
    min-height: 304px;
  }
`;

export const LogoSection = styled.div`
  display: flex;

  svg {
    color: #fdfdfd;
    stroke-width: 0;
    padding-left: 100px;

    &:hover {
      stroke: #fdfdfd;
      stroke-width: 0.7;
    }
      @media (max-width: 918px) {
        padding-left: 50px;
      }
  }
`;

export const LinksWrapper = styled.div`
  display: flex;
  gap: 140px;
  padding-right: 93px;

  @media (max-width: 918px) {
    gap: 30px;
  }
`;

export const LinksColumn = styled.div`
  display: flex;
  flex-direction: column;
  gap: 30px;

  a {
    color: #fdfdfd;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  div {
    display: flex;
    align-items: center;
    gap: 16px;
  }
`;

// Mobile Footer Styles
export const MobileLinksWrapper = styled.div`
  display: none;

  @media ${({ theme }) => theme.media.mobile} {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    width: 100%;
  }
`;

export const MobileLinkItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 0;
  border-bottom: 1px solid white;
  font: ${({ theme }) => theme.fonts.body4regular16}

  img {
    filter: brightness(0) invert(1);
    width: 24px;
    height: 24px;
  }
`;

export const MobileLogoIconsWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  a:first-child {
    svg {
    width: 79.7px;
    height: 27.98px;
    color: #fdfdfd;
    stroke-width: 0;
    margin: 0;
    padding-bottom: 10px;

    &:hover {
      stroke: #fdfdfd;
      stroke-width: 0.7;
    }
  }
  }

  div {
    display: flex;
    gap: 16px;
    padding-bottom: 10px;
  }
`;

export const MobileIconLink = styled.a`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;

  img {
    width: 100%;
    height: 100%;
  }
`;
