import React, {useEffect} from "react";
import Modal from "../Modal/Modal";
import {theme} from "../../styles/theme";
import {
    Body1_Regular,
    Body2_LinkMedium,
    Body2_Medium,
    Body2_Regular,
    Body3_Bold,
    Body4_LinkMedium,
    Body6_Regular,
    H2_Bold,
} from "../../components/Typography/Typography";
import styled from "styled-components";
import {Button} from "../../components/button/button";
import {Input} from "../../components/input/input";
import {Controller, useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {validationSchema} from "./validationSchema";
import {useSelector} from "react-redux";
import {registerUserAsync} from "../../store/auth/registrationSlice";
import {toggleModal} from "../../store/modal/modalSlice";
import {UserParamsType} from "../../store/types";
import {RootState, useAppDispatch} from "../../store/store";
import {selectorAdaptation} from "../../store/selectors/selectorAdaptation";

export default function Registration() {
    const dispatch = useAppDispatch();
    const modalId = 2; //присваиваем id к данному окну
    const {isRegistered, error} = useSelector(
        (state: RootState) => state.registration
    );
    const isDesktopStore = useSelector(selectorAdaptation);
    /**состояние экрана мобилки*/
    const {
        control,
        reset,
        handleSubmit,
        formState: {errors},
    } = useForm({
        resolver: yupResolver(validationSchema),
    });

    const onSubmit = (userParams: UserParamsType) => {
        console.log(userParams);
        dispatch(registerUserAsync(userParams)); //отправляем данные в редакс
        dispatch(toggleModal(modalId)); //закрываем окно 2
        reset(); //сбрасываем поля
    };

    useEffect(() => {
        if (isRegistered) {
            dispatch(toggleModal(4)); //открываем окно для ввода кода из мыла
            console.log("Регистрация успешна!");
        }
        if (error) {
            console.error("Ошибка сервака:", error);
        }
    }, [isRegistered, error]);

    return (
        <Modal id={modalId} showCloseButton={true}>
            {isDesktopStore ? (
                    <ModalContent>
                        <Title>
                            <H2_Bold>
                                Регистрация аккаунта
                            </H2_Bold>
                        </Title>
                        <Body1_Regular>Укажите ваши контакты</Body1_Regular>
                        <Form onSubmit={handleSubmit(onSubmit)}>
                            <Controller
                                name="surname"
                                control={control}
                                defaultValue=""
                                render={({field}) => (
                                    <Input
                                        {...field}
                                        width={"452px"}
                                        placeholder={"Фамилия"}
                                        error={errors.surname?.message}
                                    />
                                )}
                            />
                            <Controller
                                name="name"
                                control={control}
                                defaultValue=""
                                render={({field}) => (
                                    <Input
                                        {...field}
                                        width={"452px"}
                                        placeholder={"Имя"}
                                        error={errors.name?.message}
                                    />
                                )}
                            />
                            <Controller
                                name="phone"
                                control={control}
                                defaultValue=""
                                render={({field}) => (
                                    <Input
                                        {...field}
                                        width={"452px"}
                                        placeholder={"Телефон"}
                                        error={errors.phone?.message}
                                    />
                                )}
                            />
                            <Controller
                                name="email"
                                control={control}
                                defaultValue=""
                                render={({field}) => (
                                    <Input
                                        {...field}
                                        width={"452px"}
                                        placeholder={"Почта"}
                                        error={errors.email?.message}
                                    />
                                )}
                            />
                            <Body2_Medium>
                                <ButtonModal
                                    width={"392px"}
                                    color={theme.colors.secondary100}
                                    border={false}
                                    type="submit"
                                >
                                    Продолжить
                                </ButtonModal>
                            </Body2_Medium>
                        </Form>
                        <Body2_LinkMedium color={theme.colors.fontLink}>
                            У меня есть аккаунт
                        </Body2_LinkMedium>
                    </ModalContent>) :
                (<ModalContent>
                    <Title>
                        <Body3_Bold>
                            Зарегистрироваться
                        </Body3_Bold>
                    </Title>
                    <Body6_Regular>Для входа в систему введите данные</Body6_Regular>
                    <Form onSubmit={handleSubmit(onSubmit)}>
                        <Controller
                            name="surname"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    {...field}
                                    width={"312px"}
                                    placeholder={"Фамилия"}
                                    error={errors.surname?.message}
                                />
                            )}
                        />
                        <Controller
                            name="name"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    {...field}
                                    width={"312px"}
                                    placeholder={"Имя"}
                                    error={errors.name?.message}
                                />
                            )}
                        />
                        <Controller
                            name="phone"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    {...field}
                                    width={"312px"}
                                    placeholder={"Телефон"}
                                    error={errors.phone?.message}
                                />
                            )}
                        />
                        <Controller
                            name="email"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    {...field}
                                    width={"312px"}
                                    placeholder={"Почта"}
                                    error={errors.email?.message}
                                />
                            )}
                        />
                        <Body2_Regular>
                            <ButtonModal
                                width={"312px"}
                                color={theme.colors.secondary100}
                                border={false}
                                type="submit"
                            >
                                Продолжить
                            </ButtonModal>
                        </Body2_Regular>
                    </Form>
                    <Body4_LinkMedium color={theme.colors.fontLink}>
                        У меня есть аккаунт
                    </Body4_LinkMedium>
                </ModalContent>)
            }
        </Modal>
    );
}

const ModalContent = styled.div`
    width: 772px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    @media ${theme.media.tablet} {
        /**адаптивная верстка 'screen and (max-width: 992px)',*/
        width: 576px;
    }
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        width: 100%;
        max-width: 360px;
    }
`;
const Title = styled.div`
    text-align: center;
    padding-top: 24px;
    padding-bottom: 40px;
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        padding-top: 0;
        padding-bottom: 20px;
    }
`;
const Form = styled.form`
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        margin-top: 13px;
        gap: 15px;
    }
`;
const ButtonModal = styled(Button)`
    margin-top: 40px;
    margin-bottom: 24px;
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        margin-top: 7px;
        margin-bottom: 20px;
    }
`;