"use client";
import { createGlobalStyle } from 'styled-components';
import reset from 'styled-reset';

export const GlobalStyle = createGlobalStyle`
  ${reset}

  body {
    display: flex;
    justify-content: center;
    margin: 0;
    background-color: ${({ theme }) => theme.colors.backgroundWhite100};
    width: 100%;
    overflow-x: hidden; 
  }
`;
