import Image from "next/image";

type Props = {
    width?: number;
    height?: number;
    srcImg: string;
};

export const Icon = (
    ({ srcImg, width=50, height=50 }: Props) => {
        return (
                <Image
                    src={srcImg}
                    alt={srcImg}
                    width={width}
                    height={height}
                />
        );
    }
);
