import * as yup from 'yup';

export const validationSchema = yup.object().shape({
    password: yup
        .string()
        .required("Пароль обязателен")
        .min(10, "Введите не менее 10 символов латиницей")
        .matches(/[A-Z]/, "Введите заглавную букву")
        .matches(/[a-z]/, "Введите строчную букву")
        .matches(/[0-9]/, "Введите цифру")
        .matches(/[!@#$%^&*(),.?":{}|<>]/, "Введите !@#$%^&*(),.?\":{}|<>"),
    passwordRepeat: yup
        .string()
        .required("Пароль обязателен")
        .oneOf([yup.ref('password')], "Пароли не совпадают"),
});
