import React, {useEffect} from "react";
import Modal from "../Modal/Modal";
import {theme} from "../../styles/theme";
import {
    Body1_Regular,
    Body2_Medium, Body2_Regular, Body3_Bold, Body6_Regular,
    H2_Bold,
} from "../../components/Typography/Typography";
import styled from "styled-components";
import {Button} from "../../components/button/button";
import {Input} from "../../components/input/input";
import {Controller, useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {validationSchema} from "./validationSchema";
import {useSelector} from "react-redux";
import {toggleModal} from "../../store/modal/modalSlice";
import {RootState, useAppDispatch} from "../../store/store";
import {VerificationCodeType} from "../../store/types";
import {registerCodeAsync} from "../../store/auth/registrationSlice";
import {selectorAdaptation} from "../../store/selectors/selectorAdaptation";

export default function RegistrationCode() {
    const dispatch = useAppDispatch();
    const {user} = useSelector((state: RootState) => state.registration);
    const modalId = 4; //присваиваем id к данному окну
    const isDesktopStore = useSelector(selectorAdaptation);
    /**состояние экрана мобилки*/
    const {isRegisteredCode, error} = useSelector(
        (state: RootState) => state.registration
    );

    const {
        control,
        reset,
        handleSubmit,
        formState: {errors},
    } = useForm({
        resolver: yupResolver(validationSchema),
    });

    const onSubmit = (data: VerificationCodeType) => {
        const {code} = data;
        console.log(code);
        dispatch(registerCodeAsync(code));
        dispatch(toggleModal(modalId)); //закрываем окно
        reset(); //сбрасываем поля
    };

    useEffect(() => {
        if (isRegisteredCode) {
            dispatch(toggleModal(6)); //открываем окно для создания пароля
            console.log("Можно создавать пароль!");
        }
        if (error) {
            console.error("Ошибка сервака:", error);
        }
    }, [isRegisteredCode, error]);

    return (
        <Modal id={modalId} showCloseButton={true}>
            {isDesktopStore ? (<ModalContent>
                    <Title>
                        <H2_Bold>
                            Подтверждение электронной почты
                        </H2_Bold>
                    </Title>
                    <Body1_Regular>Введите код</Body1_Regular>
                    <Form onSubmit={handleSubmit(onSubmit)}>
                        <Controller
                            name="code"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    {...field}
                                    width={"452px"}
                                    placeholder={"0000"}
                                    error={errors.code?.message}
                                />
                            )}
                        />
                        <Body2_Medium>
                            Код был отправлен на ваш адрес {user?.email}
                        </Body2_Medium>
                        <Body2_Medium>
                            <ButtonModal
                                width={"392px"}
                                color={theme.colors.secondary100}
                                border={false}
                                type="submit"
                            >
                                Продолжить
                            </ButtonModal>
                        </Body2_Medium>
                    </Form>
                </ModalContent>) :
                (<ModalContent>
                    <Title>
                        <Body3_Bold>
                            Подтверждение электронной почты
                        </Body3_Bold>
                    </Title>
                    <Body6_Regular>Введите код</Body6_Regular>
                    <Form onSubmit={handleSubmit(onSubmit)}>
                        <Controller
                            name="code"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    {...field}
                                    width={"312px"}
                                    placeholder={"0000"}
                                    error={errors.code?.message}
                                />
                            )}
                        />
                        <Body6_Regular>
                            Код был отправлен на ваш адрес {user?.email}
                        </Body6_Regular>
                        <Body2_Regular>
                            <ButtonModal
                                width={"312px"}
                                color={theme.colors.secondary100}
                                border={false}
                                type="submit"
                            >
                                Продолжить
                            </ButtonModal>
                        </Body2_Regular>
                    </Form>
                </ModalContent>)
            }
        </Modal>
    );
}

const ModalContent = styled.div`
    width: 772px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    @media ${theme.media.tablet} {
        /**адаптивная верстка 'screen and (max-width: 992px)',*/
        width: 100%;
        max-width: 576px;
    }
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        width: 100%;
        max-width: 360px;
    }
`;
const Title = styled.div`
    display: flex;
    align-items: center;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        max-width: 176px;
        margin-top: 0;
        margin-bottom: 20px;
    }
`;
const Form = styled.form`
    margin-top: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
`;
const ButtonModal = styled(Button)`
    margin-top: 8px;
`;
