"use client";

import StyledComponentsRegistry from "./lib/registry";
import { GlobalStyle } from "./styles/GlobalStyle";
import { ThemeProvider } from "styled-components";
import { sfProDisplay, theme } from "./styles/theme";
import Footer from "./components/Footer/Footer";
import Header from "./components/Header/Header";
import { store } from "./store/store";
import { Provider } from "react-redux";
import RegistrationCode from "./popups/registrationCode/registrationCode";
import RegistrationPassword from "./popups/registrationPassword/registrationPassword";
import MediaQueryHandler from "./components/MediaQuery/MediaQueryHandler";
import Registration from "./popups/registration/registration";

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <Provider store={store}>
      <html lang="ru" className={sfProDisplay.className}>
        <head></head>
        <body>
          <MediaQueryHandler>
            <StyledComponentsRegistry>
              <ThemeProvider theme={theme}>
                <GlobalStyle />
                <Header />
                <div style={layoutStyles.container}>
                  <Registration/>
                  <RegistrationCode />
                  <RegistrationPassword />
                  <main style={layoutStyles.main}>{children}</main>
                  <Footer />
                </div>
              </ThemeProvider>
            </StyledComponentsRegistry>
          </MediaQueryHandler>
        </body>
      </html>
    </Provider>
  );
}

const layoutStyles: {
  container: React.CSSProperties;
  main: React.CSSProperties;
} = {
  container: {
    display: "flex",
    flexDirection: "column",
    minHeight: "100vh",
    marginTop: "100px",
    width: "100%",
    maxWidth: "1440px",
  },
  main: {
    flex: 1,
    display: "flex",
    maxWidth: "1440px",
    width: "100%",
    justifyContent: "center",
  },
};
