import {
  Dispatch,
  SetStateAction,
  ForwardRefRenderFunction,
  useState,
  forwardRef,
} from "react";
import { styled } from "styled-components";
import { Target } from "../../models/models";
import { Icon } from "../Icon/Icon";

type Props = {
  className?: string;
  width?: string;
  error?: string; // ошибка заполнения поля
  placeholder: string;
  srcImg?: string; // название иконки svg input слева
  showChevronDown?: boolean; // наличие иконки шиврона выподающего списка справа
  isMessage?: string; // сообщение, связанное с input
  setIsMessage?: Dispatch<SetStateAction<string>>; // функция для обновления сообщения
} & React.InputHTMLAttributes<HTMLInputElement>;

const Container = styled.div`
  position: relative;
  display: inline-block;
  max-height: 77px;
  box-sizing: border-box;
`;
const IconContainer = styled.span`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 28px;
`;

const IconDownContainer = styled.span`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 28px;
`;
const ErrorMessage = styled.p`
  color: red;
  position: absolute;
  top: -50%;
  transform: translateX(-20%);
  left: 62px;
  max-width: 242px;
  background: white;
  padding: 0 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 19.2px;
  letter-spacing: 0.02em;
  box-sizing: border-box;
  word-wrap: break-word;
  white-space: normal;
  margin: 0 0 3px;
`;
const InputContainer: ForwardRefRenderFunction<HTMLInputElement, Props> = (
  {
    className,
    error,
    srcImg,
    placeholder,
    showChevronDown,
    setIsMessage,
    ...props
  },
  ref
) => {
  const [isAction, setIsAction] = useState(false);
  const [isActionValue, setIsActionValue] = useState(false); // состояние ввода текста

  const handleFocus = () => (setIsAction ? setIsAction(true) : null); // функция изменения состояния фокуса инпута

  const handleBlur = () => (setIsAction ? setIsAction(false) : null); // функция изменения состояния фокуса инпута

  const onChange = ({ target }: Target) => {
    if (showChevronDown) {
      console.log(target);
      setIsActionValue(target.value.length > 0);
    }
  };

  return (
    <Container>
      <ErrorMessage style={{ display: error ? "block" : "none" }}>
        {error}
      </ErrorMessage>
      {!isActionValue && srcImg && (
        <IconDownContainer>
          <Icon width={24} height={24} srcImg={srcImg} />
        </IconDownContainer>
      )}
      <input
        onFocus={handleFocus}
        onBlur={handleBlur}
        onChange={onChange}
        type="text"
        style={{
          borderColor: error ? "red" : isAction ? "#2A35ABCC" : "#0101014d",
          padding:
            srcImg && showChevronDown && isActionValue
              ? "8px 60px 8px 24px"
              : srcImg && showChevronDown
              ? "8px 60px"
              : srcImg
              ? "8px 24px 8px 60px"
              : showChevronDown
              ? "8px 60px 8px 24px"
              : "10px 24px",
        }}
        className={className}
        {...props}
        ref={ref}
        placeholder={placeholder}
      />
      {error && !showChevronDown ? (
        <IconContainer>
          <Icon width={24} height={24} srcImg={"exclamation-circle.svg"} />
        </IconContainer>
      ) : (
        showChevronDown && (
          <IconContainer
            style={{
              transform: isAction
                ? "rotate(180deg) translateY(50%)"
                : "translateY(-50%)",
            }}
          >
            <Icon width={24} height={24} srcImg={"chevron-down.svg"} />
          </IconContainer>
        )
      )}
    </Container>
  );
};

const ComponentInput = forwardRef(InputContainer);
ComponentInput.displayName = "InputContainer";

export const Input = styled(ComponentInput)`
  width: ${({ width = "300px" }) => width};
  height: 48px;
  border-radius: 12px;
  border: 1px solid;
  font-size: 16px;
  box-sizing: border-box;
  outline: none;
`;
