import { configureStore, ThunkAction, UnknownAction } from "@reduxjs/toolkit";
import registrationReducer from "./auth/registrationSlice";
import modalReducer from "./modal/modalSlice";
import { useDispatch } from "react-redux";
import { adaptationReducer } from "./reducers/adaptationReducer";

export const store = configureStore({
  reducer: {
    registration: registrationReducer,
    modal: modalReducer,
    adaptation: adaptationReducer,
  },
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
export type AppThunk = ThunkAction<void, RootState, null, UnknownAction>;

export const useAppDispatch = () => useDispatch<AppDispatch>();
