import styled from "styled-components";

export const HeaderContainer = styled.header`
  display: flex;
  position: fixed; 
  width: 100%;
  max-width: 1440px;
  justify-content: space-between;
  align-items: center;
  background-color: ${({ theme }) => theme.colors.backgroundWhite100};
  height: 100px;
  box-shadow: 0 20px 30px rgba(1, 1, 1, 0.15);
  z-index: 100;

  @media ${({ theme }) => theme.media.mobile} {
    height: 52px; 
  }
`;

export const LogoSection = styled.div`
  display: flex;
  
  svg {
    stroke-width: 0; 
    color: ${({ theme }) => theme.colors.primary100};
    padding: 0 60px;

    &:hover {
      stroke:${({ theme }) => theme.colors.primary100};
      stroke-width: 0.7; 
    }
       @media ${({ theme }) => theme.media.mobile} {
        width: 79.7px;
        height: 27.98px;
        padding-left: 24px;
    }
  }
`;

export const LoginButton = styled.div`
  padding-right: 60px;
  @media ${({ theme }) => theme.media.mobile} {
      padding-right: 24px;
    }
`