import React from "react";
import Modal from "../Modal/Modal";
import {theme} from "../../styles/theme";
import {
    Body1_Regular,
    Body2_LinkMedium,
    Body2_Medium,
    Body2_Regular,
    Body3_Bold,
    Body4_LinkMedium,
    Body6_LinkRegular,
    Body6_Regular,
    H2_Bold,
} from "../../components/Typography/Typography";
import styled from "styled-components";
import {Button} from "../../components/button/button";
import {Input} from "../../components/input/input";
import {Controller, useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {validationSchema} from "./validationSchema";
import {useDispatch, useSelector} from 'react-redux';
import {toggleModal} from "../../store/modal/modalSlice";
import {Icon} from "../../components/Icon/Icon";
import {selectorAdaptation} from "../../store/selectors/selectorAdaptation";
//import { loginUser } from './loginSlice';

export default function Login() {
    const dispatch = useDispatch();
    const modalId = 1;
    /**присваиваем id к данному окну*/
    const isDesktopStore = useSelector(selectorAdaptation);
    /**состояние экрана мобилки*/

    const {control, reset, handleSubmit, formState: {errors}} = useForm({
        resolver: yupResolver(validationSchema),
    });

    const onSubmit = (data: any) => {
        console.log(data)
        //dispatch(loginUser(data));
        dispatch(toggleModal(modalId));//закрываем окно после передачи
        reset();//для сброса значении полей после передачи данных
    };

    const handleAnotherModal = () => {
        dispatch(toggleModal(2));
        dispatch(toggleModal(1));
    }

    return (
        <Modal
            id={modalId}
            trigger={
                isDesktopStore ? (<Button
                    color={theme.colors.primary80}
                    border={false}
                    width={"172px"}
                >
                    <Body2_Regular color={theme.colors.fontWhite}>
                        Войти
                    </Body2_Regular>
                </Button>) : (<Icon width={24} srcImg={'user.svg'}/>)
            }
            showCloseButton={true}
        >
            {isDesktopStore ? (<ModalContent>
                    <Title>
                        <H2_LinkBold>
                            Войти
                        </H2_LinkBold>
                        <H2_Bold>
                            или
                        </H2_Bold>
                        <LinkAnotherModal onClick={handleAnotherModal}>Зарегистрироваться</LinkAnotherModal>
                    </Title>
                    <Body1_Regular>Для входа в систему введите данные</Body1_Regular>
                    <Form onSubmit={handleSubmit(onSubmit)}>
                        <Controller
                            name="email"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    {...field}
                                    width={"452px"}
                                    placeholder={"Почта"}
                                    error={errors.email?.message}
                                />
                            )}
                        />
                        <Controller
                            name="password"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    type={"password"}
                                    {...field}
                                    width={"452px"}
                                    placeholder={"Введите пароль"}
                                    error={errors.password?.message}
                                />
                            )}
                        />
                        <Body2_Medium>
                            <ButtonModal
                                width={"392px"}
                                color={theme.colors.secondary100}
                                border={false}
                                type="submit"
                            >
                                Продолжить
                            </ButtonModal>
                        </Body2_Medium>
                    </Form>
                    <Body2_LinkMedium color={theme.colors.fontLink}>Забыли пароль</Body2_LinkMedium>
                </ModalContent>) :
                (<ModalContent>
                    <Title>
                        <Body3_Bold>
                            Войти
                        </Body3_Bold>
                    </Title>
                    <Body6_Regular>Для входа в систему введите данные</Body6_Regular>
                    <Form onSubmit={handleSubmit(onSubmit)}>
                        <Controller
                            name="email"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    {...field}
                                    width={"312px"}
                                    placeholder={"Почта"}
                                    error={errors.email?.message}
                                />
                            )}
                        />
                        <Controller
                            name="password"
                            control={control}
                            defaultValue=""
                            render={({field}) => (
                                <Input
                                    type={"password"}
                                    {...field}
                                    width={"312px"}
                                    placeholder={"Пароль"}
                                    error={errors.password?.message}
                                />
                            )}
                        />
                        <Body6_LinkRegular color={theme.colors.fontLink}>Забыли пароль</Body6_LinkRegular>
                        <Body2_Regular>
                            <ButtonModal
                                width={"312px"}
                                color={theme.colors.secondary100}
                                border={false}
                                type="submit"
                            >
                                Войти
                            </ButtonModal>
                        </Body2_Regular>
                    </Form>
                    <LinkAnotherModalMob color={theme.colors.fontLink} onClick={handleAnotherModal}>
                        У меня нет аккаунта
                    </LinkAnotherModalMob>
                </ModalContent>)
            }
        </Modal>
    );
}

export const H2_LinkBold = styled(H2_Bold)`
    text-decoration: underline;
`;

const ModalContent = styled.div`
    width: 772px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    @media ${theme.media.tablet} {
        /**адаптивная верстка 'screen and (max-width: 992px)',*/
        width: 100%;
        max-width: 576px;
    }
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        width: 100%;
        max-width: 360px;
    }
`;
const Title = styled.div`
    display: flex;
    text-align: center;
    gap: 20px;
    padding-top: 22px;
    padding-bottom: 40px;
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        padding-top: 0;
        padding-bottom: 20px;
    }
`;
const LinkAnotherModal = styled(H2_Bold)`
    &:hover {
        cursor: pointer;
        color: ${theme.colors.fontLink};
    }
`;
const LinkAnotherModalMob = styled(Body4_LinkMedium)`
    cursor: pointer;
`;
const Form = styled.form`
    margin-top: 20px;
    margin-bottom: -6px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        margin-top: 12px;
        gap: 16px;
    }
`;
const ButtonModal = styled(Button)`
    margin-top: 40px;
    margin-bottom: 25px;
    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        margin-top: 0;
        margin-bottom: 30px;
    }
`;