import { useMediaQuery } from "@mui/material";
import { useDispatch } from "react-redux";
import { useEffect } from "react";
import {
  setDesktopMode,
  setMobileMode,
} from "../../store/actions/actionAdaptation";

interface Props {
  children: React.ReactNode;
}

const MediaQueryHandler = ({ children }: Props) => {
  const dispatch = useDispatch();
  const isDesktop = useMediaQuery("(min-width: 577px)");

  useEffect(() => {
    if (isDesktop) {
      dispatch(setDesktopMode());
    } else {
      dispatch(setMobileMode());
    }
  }, [dispatch, isDesktop]);

  return <>{children}</>;
};

export default MediaQueryHandler;
