"use client";

import { HeaderContainer, LogoSection, LoginButton } from "./Header.styled";
import { Logo } from "../Footer/Icons/Logo";
import Login from "../../popups/loginModal/login";

export default function Header() {
  return (
    <HeaderContainer>
      <LogoSection>
        <a href="/">
          <Logo />
        </a>
      </LogoSection>
      <LoginButton>
        <Login />
      </LoginButton>
    </HeaderContainer>
  );
}
