"use client";

import { useSelector } from "react-redux";
import {
  FooterContainer,
  LogoSection,
  LinksWrapper,
  LinksColumn,
  MobileLinksWrapper,
  MobileLinkItem,
  MobileLogoIconsWrapper,
  MobileIconLink,
} from "./Footer.styled";
import { Logo } from "./Icons/Logo";
import { RootState } from "../../store/store";

export default function Footer() {
  const isDesktopStore = useSelector((state: RootState) => state.adaptation.isDesktop);

  return (
    <FooterContainer>
      {isDesktopStore ? renderDesktopFooter() : renderMobileFooter()}
    </FooterContainer>
  );
}

function renderDesktopFooter() {
  return (
    <>
      <LogoSection>
        <a href="#">
          <Logo />
        </a>
      </LogoSection>
      <LinksWrapper>
        <LinksColumn>
          <a href="#">О проекте</a>
          <a href="#">Партнерство</a>
          <a href="#">Новости</a>
        </LinksColumn>
        <LinksColumn>
          <a href="#">События</a>
          <a href="#">Избранное</a>
          <a href="#">Поддержка</a>
        </LinksColumn>
        <LinksColumn>
          <a href="#">Контакты</a>
          <a href="#">Мы в соцсетях</a>
          <div>
            <a href="#">
              <img src="/telegram.svg" alt="Telegram Icon" />
            </a>
            <a href="#">
              <img src="/vk.svg" alt="VK Icon" />
            </a>
            <a href="#">
              <img src="/ok.svg" alt="OK Icon" />
            </a>
          </div>
        </LinksColumn>
      </LinksWrapper>
    </>
  );
}


function renderMobileFooter() {
  return (
    <>
      <MobileLinksWrapper>
        <MobileLinkItem>
          Информация
          <img src="/chevron-down.svg" alt="Arrow Down" />
        </MobileLinkItem>
        <MobileLinkItem>
          О проекте
          <img src="/chevron-down.svg" alt="Arrow Down" />
        </MobileLinkItem>
        <MobileLinkItem>
          Контакты
          <img src="/chevron-down.svg" alt="Arrow Down" />
        </MobileLinkItem>
        <MobileLinkItem>
          Новости
          <img src="/chevron-down.svg" alt="Arrow Down" />
        </MobileLinkItem>
      </MobileLinksWrapper>
      <MobileLogoIconsWrapper>
        <a href="#">
          <Logo />
        </a>
        <div>
          <MobileIconLink href="#">
            <img src="/telegram.svg" alt="Telegram Icon" />
          </MobileIconLink>
          <MobileIconLink href="#">
            <img src="/vk.svg" alt="VK Icon" />
          </MobileIconLink>
          <MobileIconLink href="#">
            <img src="/ok.svg" alt="OK Icon" />
          </MobileIconLink>
        </div>
      </MobileLogoIconsWrapper>
    </>
  );
}
